import React, { useEffect, useState } from 'react';
import { NavLink } from 'react-router-dom';
import { useParams } from 'react-router-dom';
import { Box, Link, Text, HStack } from '@chakra-ui/react';


function Footer() {
  const [isVisible, setIsVisible] = useState(false);

  useEffect(() => {
    function handleScroll() {
      const scrollPosition = window.pageYOffset;
      const windowHeight = window.innerHeight;
      const documentHeight = document.documentElement.scrollHeight;

      if (scrollPosition + windowHeight >= documentHeight) {
        setIsVisible(true);
      } else {
        setIsVisible(false);
      }
    }

    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <Box
      position="fixed"
      bottom= '0'
      width="100%"
      height="500px"
      zIndex="-1"
      backgroundColor="#B6D2B2"
      color="black"
      textAlign="center"
      // padding="20px"
      >
        <Box padding="10px"  >
          <Text fontSize="9xl" fontWeight="bold" mb={4}>
            SOFTWARE <br/> DEVELOPER
          </Text>
          <HStack spacing={4}justifyContent="center">
            <Link
              target="_blank"
              rel="noopener noreferrer"
              href="https://www.linkedin.com/in/adiel-eichenstein/"
            >
              LinkedIn
            </Link>
            <Link
              target="_blank"
              rel="noopener noreferrer"
              href="https://medium.com/@adiel.eich"
            >
              Medium
            </Link>
            <Link
              target="_blank"
              rel="noopener noreferrer"
              href="https://github.com/adielhorodna/"
            >
              GitHub
            </Link>
          </HStack>
          <Text fontSize="2xl" fontWeight="bold" mt={4}>
            adiel.eich@gmail.com
          </Text>
        
        </Box>
    </Box>
  );
}

export default Footer;

