import React, { useEffect, useState } from 'react';
import { NavLink } from 'react-router-dom';
import { useParams } from 'react-router-dom';
import { Box, Heading, Text, Button,IconButton, Link, Flex } from '@chakra-ui/react';


import {  UnorderedList, ListItem,  } from "@chakra-ui/react";
import { CloseIcon } from '@chakra-ui/icons';

function Projects() {
  const [showVideo1, setShowVideo1] = useState(false);
  const [showVideo2, setShowVideo2] = useState(false);
  const [showVideo3, setShowVideo3] = useState(false);

  const handlePlayVideo1 = () => setShowVideo1(true);
  const handlePlayVideo2 = () => setShowVideo2(true);
  const handlePlayVideo3 = () => setShowVideo3(true);

  const handleCloseVideo1 = () => setShowVideo1(false);
  const handleCloseVideo2 = () => setShowVideo2(false);
  const handleCloseVideo3 = () => setShowVideo3(false);

  return (
    <Box as="section" id="projects" mx="auto" maxWidth="800px">
      <Box className="projects-container">
        <Box className="project-container project-card" mx="auto">
          <Heading as="h3" fontSize="8xl" fontWeight="bold" color="black">
            1st Year
          </Heading>
          <Text as="h4" textAlign="center" color="#B6D2B2" fontWeight="bold" mb={4}>
            HTML | CSS | JAVASCRIPT | REACT | RUBY | RAILS | CHAKRA UI
          </Text>
          <Text color="black" textAlign="center" mb={4}>
            A site for new mothers to see baby milestones and tips for each month, have the
            ability to post to support other mothers, and jot down personal memories all
            through their baby’s first year.
          </Text>
          <UnorderedList mb={4} listStylePosition="inside" textAlign="left" pl={4}>
            {/* Your list items */}
          </UnorderedList>
          {!showVideo1 ? (
            <Button onClick={handlePlayVideo1}>Play Video</Button>
          ) : (
            <>
              <Button onClick={handleCloseVideo1}>Close Video</Button>
              <iframe
                width="560"
                height="315"
                src="https://www.youtube.com/embed/IjVc60DlC2k"
                title="YouTube video player"
                frameborder="0"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
                allowfullscreen
              />
            </>
          )}
        </Box>

        <Box className="project-container project-card">
          <Heading as="h3" fontSize="8xl" fontWeight="bold" color="black">
            StayOver
          </Heading>
          <Text as="h4" textAlign="center" color="#B6D2B2" fontWeight="bold" mb={4}>
            HTML | CSS | JAVASCRIPT | REACT | RUBY
          </Text>
          <Text color="black" textAlign="center" mb={4}>
            StayOver is a site that allows a user to find and book home accommodations.
          </Text>
          <UnorderedList mb={4} listStylePosition="inside" textAlign="left" pl={4}>
            {/* Your list items */}
          </UnorderedList>
          {!showVideo2 ? (
            <Button onClick={handlePlayVideo2}>Play Video</Button>
          ) : (
            <>
              <Button onClick={handleCloseVideo2}>Close Video</Button>
              <iframe
                width="560"
                height="315"
                src="https://www.youtube.com/embed/UkUrm1E1Xmg"
                title="YouTube video player"
                frameborder="0"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
                allowfullscreen
              />
            </>
          )}
        </Box>

        <Box className="project-container project-card">
          <Heading as="h3" fontSize="8xl" fontWeight="bold" color="black">
            Pick a Pup
          </Heading>
          <Text as="h4" textAlign="center" fontWeight="bold" color="#B6D2B2" mb={4}>
            HTML | CSS | JAVASCRIPT | REACT
          </Text>
          <Text color="black" textAlign="center" mb={4}>
            An app where you can adopt puppies from the Animal Adoption Center.
          </Text>
          <UnorderedList mb={4} listStylePosition="inside" textAlign="left" pl={4}>
            {/* Your list items */}
          </UnorderedList>
          {!showVideo3 ? (
            <Button onClick={handlePlayVideo3}>Play Video</Button>
          ) : (
            <>
              <Button onClick={handleCloseVideo3}>Close Video</Button>
              <iframe
                width="560"
                height="315"
                src="https://www.youtube.com/embed/SJolXzZ1tmI"
                title="YouTube video player"
                frameborder="0"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
                allowfullscreen
              />
            </>
          )}
        </Box>
      </Box>
    </Box>
  );
}

export default Projects;


